let pdfTextContent = '';
let currentFontSize = 14;

Office.onReady((info) => {
  if (info.host === Office.HostType.Word) {
    initializeEventListeners();
    setupDragAndDrop();
  }
});

// Inicializar todos los event listeners
function initializeEventListeners() {
    document.getElementById("pdf-file").addEventListener("change", handlePdfUpload);
    document.getElementById("select-file-btn").addEventListener("click", () => {
        document.getElementById("pdf-file").click();
    });
    document.getElementById("search-button").addEventListener("click", searchAndInsertArticle);
    document.getElementById("insert-selection-button").addEventListener("click", insertSelectedText);
    document.getElementById("zoom-in").addEventListener("click", () => adjustFontSize(2));
    document.getElementById("zoom-out").addEventListener("click", () => adjustFontSize(-2));
    
    // Mejorar la experiencia de b√∫squeda con Enter
    document.getElementById("search-term").addEventListener("keypress", (e) => {
        if (e.key === 'Enter') {
            searchAndInsertArticle();
        }
    });
}

// Configurar drag and drop
function setupDragAndDrop() {
    const uploadArea = document.getElementById('upload-area');
    
    ['dragenter', 'dragover', 'dragleave', 'drop'].forEach(eventName => {
        uploadArea.addEventListener(eventName, preventDefaults, false);
    });
    
    ['dragenter', 'dragover'].forEach(eventName => {
        uploadArea.addEventListener(eventName, highlight, false);
    });
    
    ['dragleave', 'drop'].forEach(eventName => {
        uploadArea.addEventListener(eventName, unhighlight, false);
    });
    
    uploadArea.addEventListener('drop', handleDrop, false);
}

function preventDefaults(e) {
    e.preventDefault();
    e.stopPropagation();
}

function highlight(e) {
    document.getElementById('upload-area').classList.add('drag-over');
}

function unhighlight(e) {
    document.getElementById('upload-area').classList.remove('drag-over');
}

function handleDrop(e) {
    const dt = e.dataTransfer;
    const files = dt.files;
    
    if (files.length > 0) {
        const file = files[0];
        if (file.type === "application/pdf") {
            // Simular la selecci√≥n del archivo
            const fileInput = document.getElementById("pdf-file");
            const dataTransfer = new DataTransfer();
            dataTransfer.items.add(file);
            fileInput.files = dataTransfer.files;
            
            // Disparar el evento de cambio
            handlePdfUpload({ target: { files: [file] } });
        } else {
            showMessage('Por favor, selecciona un archivo PDF v√°lido.', 'error');
        }
    }
}

// Funci√≥n mejorada para manejar la carga del PDF
function handlePdfUpload(event) {
    const file = event.target.files[0];
    if (!file || file.type !== "application/pdf") {
        showMessage('Por favor, selecciona un archivo PDF v√°lido.', 'error');
        return;
    }

    // Validar tama√±o del archivo (10MB m√°ximo)
    const maxSize = 10 * 1024 * 1024; // 10MB en bytes
    if (file.size > maxSize) {
        showMessage('El archivo es demasiado grande. M√°ximo 10MB permitido.', 'error');
        return;
    }

    const fileReader = new FileReader();
    const loadingSpinner = document.getElementById('loading-spinner');
    const mainControls = document.getElementById('main-controls');
    const uploadStep = document.getElementById('upload-step');

    // Mostrar estado de carga
    loadingSpinner.style.display = 'block';
    mainControls.style.display = 'none';
    uploadStep.style.display = 'none';
    pdfTextContent = '';

    fileReader.onload = function() {
        const typedarray = new Uint8Array(this.result);
        
        pdfjsLib.getDocument(typedarray).promise.then(pdf => {
            let textPromises = [];
            const totalPages = pdf.numPages;
            
            // Actualizar informaci√≥n del documento
            updateDocumentInfo(file.name, totalPages);
            
            for (let i = 1; i <= pdf.numPages; i++) {
                textPromises.push(
                    pdf.getPage(i).then(page => page.getTextContent())
                );
            }
            return Promise.all(textPromises);
        }).then(textContents => {
            // Procesar y almacenar el texto
            pdfTextContent = textContents.map((content, index) => {
                const pageText = content.items.map(item => item.str).join(' ');
                return `=== P√ÅGINA ${index + 1} ===\n${pageText}`;
            }).join('\n\n');
            
            // Renderizar vista previa
            renderPdfAsText(textContents);

            // Ocultar loading y mostrar controles
            loadingSpinner.style.display = 'none';
            mainControls.style.display = 'block';
            
            showMessage('Documento cargado y procesado correctamente.', 'success');
        }).catch(error => {
            console.error("Error al procesar el PDF:", error);
            loadingSpinner.style.display = 'none';
            uploadStep.style.display = 'block';
            showMessage('Error al procesar el PDF. Verifica que el archivo no est√© da√±ado.', 'error');
        });
    };
    
    fileReader.readAsArrayBuffer(file);
}

// Actualizar informaci√≥n del documento
function updateDocumentInfo(fileName, totalPages) {
    document.getElementById('document-name').textContent = fileName;
    document.getElementById('document-stats').textContent = 
        `${totalPages} p√°gina${totalPages !== 1 ? 's' : ''} ‚Ä¢ Procesado correctamente`;
}

// Renderizar PDF como texto mejorado
function renderPdfAsText(textContents) {
    const viewer = document.getElementById('pdf-viewer');
    viewer.innerHTML = '';
    
    textContents.forEach((content, pageIndex) => {
        // Crear separador de p√°gina
        if (pageIndex > 0) {
            const separator = document.createElement('div');
            separator.style.cssText = `
                border-top: 2px solid var(--border-color);
                margin: 20px 0;
                padding-top: 15px;
                font-weight: 600;
                color: var(--neutral-secondary);
                font-size: 12px;
            `;
            separator.textContent = `--- P√ÅGINA ${pageIndex + 1} ---`;
            viewer.appendChild(separator);
        }
        
        const pageDiv = document.createElement('div');
        pageDiv.style.cssText = `
            margin-bottom: 20px;
            padding: 15px;
            background: ${pageIndex % 2 === 0 ? '#fafafa' : '#ffffff'};
            border-radius: 4px;
            border-left: 3px solid var(--primary-color);
            user-select: text;
            cursor: text;
        `;
        
        const pageText = content.items.map(item => item.str).join(' ');
        pageDiv.textContent = pageText;
        
        // Resaltar art√≠culos autom√°ticamente
        highlightArticles(pageDiv);
        
        viewer.appendChild(pageDiv);
    });
    
    // Aplicar tama√±o de fuente actual
    viewer.style.fontSize = currentFontSize + 'px';
}

// Funci√≥n para resaltar art√≠culos en el texto
function highlightArticles(element) {
    const articleRegex = /((?:Art√≠culo|Articulo|Art\.)\s+\d+[^\n]*)/gi;
    const text = element.textContent;
    const matches = text.match(articleRegex);
    
    if (matches) {
        let highlightedText = text;
        matches.forEach(match => {
            highlightedText = highlightedText.replace(
                match, 
                `<mark style="background: #fff3cd; padding: 2px 4px; border-radius: 3px; font-weight: 600;">${match}</mark>`
            );
        });
        element.innerHTML = highlightedText;
    }
}

// Ajustar tama√±o de fuente
function adjustFontSize(change) {
    currentFontSize = Math.max(10, Math.min(24, currentFontSize + change));
    const viewer = document.getElementById('pdf-viewer');
    viewer.style.fontSize = currentFontSize + 'px';
    
    // Feedback visual
    showMessage(`Tama√±o de fuente: ${currentFontSize}px`, 'info', 1500);
}

// B√∫squeda mejorada de art√≠culos
function searchAndInsertArticle() {
    const searchTerm = document.getElementById("search-term").value.trim();
    const resultElement = document.getElementById("search-result");

    if (!pdfTextContent) {
        showMessage("Primero debes cargar un PDF.", 'error');
        return;
    }
    if (!searchTerm) {
        showMessage("Por favor, ingresa un n√∫mero de art√≠culo.", 'error');
        return;
    }

    // Limpiar resultado anterior
    resultElement.textContent = '';
    
    // Mostrar estado de b√∫squeda
    showMessage("Buscando art√≠culo...", 'info');

    // Expresi√≥n regular mejorada y m√°s flexible
    const patterns = [
        `(?:Art√≠culo|Articulo|Art\\.)\\s+${searchTerm}(?:\\s|\\.|:)[\\s\\S]*?(?=(?:Art√≠culo|Articulo|Art\\.)\\s+\\d|CAP√çTULO|T√çTULO|===|$)`,
        `Art\\s+${searchTerm}[\\s\\S]*?(?=Art\\s+\\d|CAP√çTULO|T√çTULO|===|$)`,
        `${searchTerm}\\.[\\s\\S]*?(?=\\d+\\.|CAP√çTULO|T√çTULO|===|$)`
    ];

    let foundText = null;
    let matchPattern = null;

    // Probar cada patr√≥n
    for (let pattern of patterns) {
        const regex = new RegExp(pattern, 'i');
        const match = pdfTextContent.match(regex);
        
        if (match && match[0]) {
            foundText = match[0].trim();
            matchPattern = pattern;
            break;
        }
    }

    if (foundText) {
        // Limpiar el texto encontrado
        foundText = cleanExtractedText(foundText);
        
        showMessage(`‚úÖ Art√≠culo ${searchTerm} encontrado e insertado correctamente.`, 'success');
        insertTextIntoWord(foundText);
        
        // Resaltar en la vista previa
        highlightSearchResult(searchTerm);
    } else {
        showMessage(`‚ùå Art√≠culo "${searchTerm}" no encontrado. Verifica el n√∫mero e intenta de nuevo.`, 'error');
    }
}

// Limpiar texto extra√≠do
function cleanExtractedText(text) {
    return text
        .replace(/===[^===]*===/g, '') // Remover separadores de p√°gina
        .replace(/\s+/g, ' ') // Normalizar espacios
        .replace(/^\s+|\s+$/g, '') // Trim
        .replace(/([.!?])\s*([A-Z√Å√â√ç√ì√ö√ë])/g, '$1\n\n$2'); // Mejorar formato de p√°rrafos
}

// Resaltar resultado de b√∫squeda en la vista previa
function highlightSearchResult(searchTerm) {
    const viewer = document.getElementById('pdf-viewer');
    const regex = new RegExp(`((?:Art√≠culo|Articulo|Art\\.)\\s+${searchTerm}[^\\n]*)`, 'gi');
    
    const content = viewer.innerHTML;
    const highlightedContent = content.replace(regex, 
        '<span style="background: #ffeb3b; padding: 3px 6px; border-radius: 4px; font-weight: bold; animation: pulse 2s;">$1</span>'
    );
    
    viewer.innerHTML = highlightedContent;
    
    // Scroll al resultado
    const highlighted = viewer.querySelector('span[style*="ffeb3b"]');
    if (highlighted) {
        highlighted.scrollIntoView({ behavior: 'smooth', block: 'center' });
    }
}

// Insertar texto seleccionado mejorado
function insertSelectedText() {
    const selectedText = window.getSelection().toString().trim();
    
    if (selectedText) {
        if (selectedText.length < 10) {
            showMessage("‚ö†Ô∏è El texto seleccionado es muy corto. ¬øEst√°s seguro de que es correcto?", 'warning');
        }
        
        const cleanedText = cleanExtractedText(selectedText);
        insertTextIntoWord(cleanedText);
        showMessage(`‚úÖ Texto seleccionado insertado correctamente (${selectedText.length} caracteres).`, 'success');
        
        // Limpiar selecci√≥n
        window.getSelection().removeAllRanges();
    } else {
        showMessage("‚ùå Por favor, selecciona primero un fragmento de texto de la vista previa.", 'error');
    }
}

// Funci√≥n gen√©rica mejorada para insertar texto en Word
function insertTextIntoWord(textToInsert) {
    Word.run(async (context) => {
        try {
            const selection = context.document.getSelection();
            
            // Insertar texto con formato mejorado
            const insertedText = selection.insertText(textToInsert + '\n\n', Word.InsertLocation.end);
            
            // Aplicar formato b√°sico
            insertedText.font.name = "Calibri";
            insertedText.font.size = 11;
            insertedText.paragraphFormat.spaceAfter = 6;
            
            await context.sync();
            
            showMessage("‚úÖ Texto insertado correctamente en el documento.", 'success');
        } catch (error) {
            console.error("Error al insertar texto:", error);
            showMessage("‚ùå Error al insertar texto. Verifica que Word est√© disponible.", 'error');
        }
    }).catch(function (error) {
        console.error("Error en Word.run:", error);
        showMessage("‚ùå Error de conexi√≥n con Word.", 'error');
    });
}

// Sistema mejorado de mensajes
function showMessage(message, type = 'info', duration = 4000) {
    const resultElement = document.getElementById("search-result");
    
    // Limpiar clases anteriores
    resultElement.className = 'result-message';
    
    // A√±adir clase seg√∫n el tipo
    resultElement.classList.add(type);
    resultElement.textContent = message;
    
    // Auto-ocultar mensajes despu√©s del tiempo especificado
    if (duration > 0) {
        setTimeout(() => {
            resultElement.textContent = '';
            resultElement.className = 'result-message';
        }, duration);
    }
}

// Agregar animaci√≥n de pulso para elementos destacados
const style = document.createElement('style');
style.textContent = `
    @keyframes pulse {
        0% { transform: scale(1); }
        50% { transform: scale(1.05); }
        100% { transform: scale(1); }
    }
`;
document.head.appendChild(style);

// Mejorar accesibilidad con atajos de teclado
document.addEventListener('keydown', (e) => {
    // Ctrl/Cmd + Enter para buscar
    if ((e.ctrlKey || e.metaKey) && e.key === 'Enter') {
        if (document.getElementById("search-term") === document.activeElement) {
            searchAndInsertArticle();
        }
    }
    
    // Ctrl/Cmd + I para insertar selecci√≥n
    if ((e.ctrlKey || e.metaKey) && e.key === 'i') {
        e.preventDefault();
        insertSelectedText();
    }
    
    // Ctrl/Cmd + Plus/Minus para zoom
    if ((e.ctrlKey || e.metaKey) && e.key === '+') {
        e.preventDefault();
        adjustFontSize(2);
    }
    
    if ((e.ctrlKey || e.metaKey) && e.key === '-') {
        e.preventDefault();
        adjustFontSize(-2);
    }
});

// Inicializaci√≥n adicional cuando el DOM est√© listo
document.addEventListener('DOMContentLoaded', () => {
    // Configurar tooltips para mejor UX
    const buttons = document.querySelectorAll('button');
    buttons.forEach(button => {
        if (!button.title && button.textContent) {
            const text = button.textContent.trim();
            if (text.includes('Buscar')) {
                button.title = 'Buscar art√≠culo e insertar en el documento (Ctrl+Enter)';
            } else if (text.includes('Insertar')) {
                button.title = 'Insertar texto seleccionado (Ctrl+I)';
            }
        }
    });
    
    // A√±adir informaci√≥n sobre atajos de teclado
    const footer = document.querySelector('.footer-text');
    if (footer) {
        footer.innerHTML += ' ‚Ä¢ <span title="Ctrl+Enter: Buscar, Ctrl+I: Insertar, Ctrl++/-: Zoom">Atajos disponibles</span>';
    }
});